import React from 'react';
import {connect} from 'react-redux';

export class Second extends React.Component{

  render = ()=>{
    return <div>
      <h1 className="comp-class">{this.props.anItem}</h1>
      <div><script language="JavaScript" src="http://www.tfl.gov.uk/tfl/syndication/widgets/tubemap/tubemap-iframe-stretchy.js"></script></div>
    </div>
  }
}

function select(state){
  return {
    anItem: state.get('anItem')
  }
};

export const SecondX = connect(select)(Second);



