import {List, Map, fromJS} from 'immutable';

const INIT_STATE = Map();

export default function(state = INIT_STATE, action){

  switch (action.type) {

    case 'DO_SOMETHING': {
      return state.set('anItem', action.val);
    }

    default:
      return state;
  }

};



